.backgroundimage {
  width: 90vw;
  height: 50vh;
  opacity: 0; /* start opacity na 0 za fade efekt */
  border-radius: 0.5rem;
  border: 2px solid rgb(158, 255, 158);
  box-shadow: 0 0 15px rgba(158, 255, 158, 0.4),
    0 0 30px rgba(158, 255, 158, 0.2);
  transform: translateX(-30vw); /* start pozicija */
  transition: opacity 1s ease, transform 1s ease;
}

.animate {
  opacity: 1;
  transform: translateY(0);
}
.location {
  transform: translateY(5vh);
}
