@media (max-width: 576px) {
  .footer {
    background: rgba(0, 0, 0, 0.779);
    color: #9eff9e; /* svijetlo zelena */
    padding: 2rem 1rem;
    font-family: "Dancing Script", cursive;
    height: 40vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 2rem;
    align-items: flex-start;
  }

  .brand h3 {
    margin: 0;
    font-size: 1.7rem;
  }

  .brand p {
    font-size: 0.9rem;
    margin-top: 0.3rem;
    color: rgba(158, 255, 158, 0.8);
  }

  .links {
    display: flex;
    gap: 3rem;
    flex-wrap: wrap;
  }

  .links h4 {
    margin-bottom: 0.5rem;
  }

  .links ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .links li {
    margin-bottom: 0.3rem;
  }

  .links a {
    color: #9eff9e;
    text-decoration: none;
    transition: color 0.3s;
  }

  .links a:hover {
    color: #fff;
  }

  .social li {
    display: block;
    margin-bottom: 0.3rem;
  }

  /* --- Responsive za mobilne ureaje --- */

  .container {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1.5rem;
  }

  .links {
    flex-direction: column;
    gap: 1rem;
  }
  .socialmedialinks {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  p,
  a,
  li {
    font-family: "Josefin Sans", sans-serif;
    font-optical-sizing: auto;
    /* font-weight: <weight>; */
    font-style: normal;
  }
}

