@media (max-width: 576px) {
  body {
    overflow-x: hidden;
  }
  .aboutsection {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.779);
  }
  .naslov {
    margin-top: 10%;
    position: absolute;
    font-family: "Dancing Script", cursive;
    color: white;
  }
  .backgroundimage {
    width: 80%;
    height: 40%;
    margin-top: 10%;
    object-fit: cover;
    object-position: center;
    object-position: center;
    opacity: 0.7 !important;

    border-radius: 0.5rem;
    background: rgba(158, 255, 158, 0.15);

    font-family: "Dancing Script", cursive;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
    border: 2px solid rgb(158, 255, 158);
    box-shadow: 0 0 15px rgba(158, 255, 158, 0.4),
      0 0 30px rgba(158, 255, 158, 0.2);
  }

  .animate {
    animation: fadeUp 3s ease-out forwards;
  }

  @keyframes fadeUp {
    0% {
      opacity: 0;
      transform: translateX(-30px);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }

  .p {
    width: 80%;
    height: auto;
    /* position: relative; */
    margin-top: 10%;
    object-fit: cover;
    object-position: center;
    object-position: center;
    opacity: 0.9;
    color: white;
    border-radius: 0.5rem;
    background: rgba(158, 255, 158, 0.15);

    font-family: "Josefin Sans", sans-serif;
    font-optical-sizing: auto;
    font-size: 180%;
    font-style: normal;
    text-align: center;
    cursor: pointer;
    border: 2px solid rgb(158, 255, 158);
    box-shadow: 0 0 15px rgba(158, 255, 158, 0.4),
      0 0 30px rgba(158, 255, 158, 0.2);
  }

  .animate-p {
    animation: fadeUp 3s ease-out forwards;
  }
  @keyframes fadeDown {
    0% {
      opacity: 0;
      transform: translateX(30px);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

