.galleryWrapper {
  position: relative;
  width: 100%;
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.779);
}

.image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.prev,
.next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 3rem;
  color: white;
  cursor: pointer;
  user-select: none;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 50%;
}

.prev {
  left: 2rem;
}

.next {
  right: 2rem;
}
