body,
html {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}

.backgroundimages {
  position: relative;
  display: flex;
  justify-content: center;
  background: rgb(0, 0, 0);
  overflow: hidden;
  align-items: center;
}

.backgroundimage {
  width: 100%;
  height: 100vh;
  margin: 0;
  object-fit: cover;
  opacity: 0.5;
  transition: opacity 4s ease-in-out;
}

.backgroundnaslov {
  position: absolute;
  margin-bottom: 30vh;
}

.naslov {
  font-family: "Dancing Script";
  color: rgb(255, 255, 255);
  font-size: 5rem;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeUp 1.5s ease-out forwards;
}

@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 0.5;
    transform: translateY(0);
  }
}

.section {
  position: absolute;
  display: flex;
  justify-content: center;
}

.podnaslov {
  font-family: "Dancing Script", cursive;
  color: rgb(158, 255, 158);
  font-size: 2rem;
  opacity: 0;
  transform: translateY(-20px);
  animation: fadeDown 1.5s ease-out forwards;
}

.podnaslov::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(to right, transparent, white, transparent);
}

@keyframes fadeDown {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.button {
  position: absolute;
  margin-top: 11rem;
  width: 50vw;
  height: 5vh;
  border-radius: 2.5rem;
  background: rgba(158, 255, 158, 0.15);
  color: rgb(158, 255, 158);
  font-family: "Josefin Sans", sans-serif !important;
  font-optical-sizing: auto;
  font-size: 130%;
  font-style: normal;
  text-align: center;
  cursor: pointer;
  border: 2px solid rgb(158, 255, 158);
  box-shadow: 0 0 15px rgba(158, 255, 158, 0.4),
    0 0 30px rgba(158, 255, 158, 0.2);
  opacity: 0;
  transform: translateY(20px);
  animation: fadeUp 1s ease-out forwards;
  animation-delay: 2s;
  transition: all 0.3s ease;
}

.button:hover {
  background: rgba(158, 255, 158, 0.3);
  color: white;
  box-shadow: 0 0 20px rgba(158, 255, 158, 0.6),
    0 0 40px rgba(158, 255, 158, 0.4);
  transform: translateY(-3px);
}

@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeDown {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

